import { RequestSender, Response } from '@bigcommerce/request-sender';

import {
    ContentType,
    INTERNAL_USE_ONLY,
    RequestOptions,
    SDK_VERSION_HEADERS,
} from '../common/http-request';

import PaymentMethod from './payment-method';

interface LoadPaymentMethodRequestOptions extends RequestOptions {
    body?: string;
    headers: { Authorization: string; [key: string]: string };
}

interface LoadPaymentMethodResponse<T = any> {
    data: {
        site: {
            paymentWalletWithInitializationData: {
                clientToken?: string;
                initializationData?: T;
            };
        }
    }
}

export default class PaymentMethodRequestSender {
    constructor(private _requestSender: RequestSender) {}

    loadPaymentMethods({ timeout, params }: RequestOptions = {}): Promise<
        Response<PaymentMethod[]>
    > {
        const url = '/api/storefront/payments';

        return this._requestSender.get(url, {
            timeout,
            headers: {
                Accept: ContentType.JsonV1,
                'X-API-INTERNAL': INTERNAL_USE_ONLY,
                ...SDK_VERSION_HEADERS,
            },
            params,
        });
    }

    loadPaymentMethod(
        methodId: string,
        { timeout, params }: RequestOptions = {},
    ): Promise<Response<PaymentMethod>> {
        const url = `/api/storefront/payments/${methodId}`;

        return this._requestSender.get(url, {
            timeout,
            headers: {
                Accept: ContentType.JsonV1,
                'X-API-INTERNAL': INTERNAL_USE_ONLY,
                ...SDK_VERSION_HEADERS,
            },
            params,
        });
    }

    loadPaymentMethodRequest(
        methodId: string,
        options: LoadPaymentMethodRequestOptions,
    ): Promise<Response<PaymentMethod>> {
        const url = `/graphql`;

        const graphQLQuery = `
            query {
                site {
                    paymentWalletWithInitializationData(filter: {paymentWalletEntityId: "${methodId}"}) {
                        clientToken
                        initializationData
                    }
                }
            }
        `;

        const requestOptions: LoadPaymentMethodRequestOptions = {
            headers: {
                ...options.headers,
                'Content-Type': 'application/json',
            },
            // @ts-ignore
            body: {
                query: graphQLQuery
            },
        };

        return this._requestSender
            .post<LoadPaymentMethodResponse>(url, {
                ...requestOptions,
            })
            .then((response) => {
                return this.transformToPaymentMethodResponse(response)
            });
    }

    private transformToPaymentMethodResponse(
        response: Response<LoadPaymentMethodResponse>,
    ): Response<PaymentMethod> {
        const {
            body: { data: { site: { paymentWalletWithInitializationData } } },
        } = response;

        const body = Object.entries(paymentWalletWithInitializationData).reduce<PaymentMethod>(
            // @ts-ignore
            (acc: Record<string, any>, [key, value]) => {
                acc[key] = value ? JSON.parse(atob(value)) : value;

                return acc;
            },
            {},
        );

        return {
            ...response,
            // @ts-ignore
            body: {
                ...body,
                id: 'paypalcommerce.paypal'
            },
        };
    }
}
